import { store, view } from '@risingstack/react-easy-state';


class Counter extends React.Component {
  counter = store({
    num: 0,
    increment: () => this.counter.num++,
    decrement: () => this.counter.num--,
  });

  componentDidMount() {
    this.autoUpdate();
  }

  autoUpdate() {
    const date = new Date();
    const hour = date.getHours();
    if (hour >= 9 && hour <= 17) {
      this.counter.increment();
    }
    if (hour <= 8 || hour >= 18) {
      this.counter.decrement();
    }
    setTimeout(this.autoUpdate.bind(this), 3600000);
  }

  render() {
    return (
      <div style={styles.container}>
        <div>
          <div style={styles.title}>
            {`Simple Counter`}
          </div>
          <h3 className="subtitle">By Adam Gienapp</h3>
        </div>

        <p>{this.counter.num}</p>

        <button onClick={this.counter.increment}>+</button>
        <button onClick={this.counter.decrement}>-</button>

        <style jsx global>{`
          body {
            background: steelblue;
            color: #fff;
          }
        `}</style>
      </div>
    );
  }
}

export default view(Counter);


const styles = {
  container: {
    width: "70%",
    minWidth: "400px",
    margin: "0 auto",
  },

  title: {
    width: "100%",
    fontSize: "60px",
    marginBottom: "0",
    textAlign: "justify",
  },

  subTitle: {
    marginTop: "0",
    textAlign: "right",
  }
}